.region {
  position: fixed;
  top: 12.5px;
  left: 289px;
  width: 92px;
  height: 41px;
  background: #f2f7fb 0% 0% no-repeat padding-box;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  font-size: 13px;
  border: 2px solid #0062af;
  border-radius: 9px;
  color: #0062af;
  font: normal normal 800 14px/17px NanumSquare;
}
.region:hover {
  background-color: #e5ebef;
}

.regionExp {
  position: fixed;
  top: 8px;
  left: 285px;
  width: 300px;
  height: 265px;
  background: var(--unnamed-color-ffffff) 0% 0% no-repeat padding-box;
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 0px 3px 6px #00000029;
  border: 1px solid #707070;
  border-radius: 9px;
  z-index: 1;
  font-size: 13px;
  border: 2px solid #0062af;
}

.regionExp.exp1 {
  width: 438px;
}

.regionExp.exp2 {
  height: 50px;
}

.city {
  position: absolute;
  top: 17px;
  left: 110px;
  width: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.reg_ttl,
.reg_ttl {
  margin-right: 8px;
}

.county {
  position: absolute;
  top: 17px;
  left: 250px;
  width: 110px;
  /* border: solid 1px #cccccc; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.close {
  position: absolute;
  top: 11px;
  left: 240px;
  font-size: 15px;
  color: #707070;
  padding: 5px;
  border-radius: 9px;
}
.close.exp1 {
  left: 380px;
}

.close:hover {
  background-color: #e5ebef;
  cursor: pointer;
}
